
























































export default {
    props: {
        idx: {
            type: Number
        },
        text: {
            type: String
        },
        addText: {
            type: String
        },
        plus: {
            default: false
        }
    },
    data() {
        return {
            
        }
    },
    methods: {
        addComponent() {
            this.$nuxt.$emit('addComponent');
        },
        removeComponent() {
            this.$nuxt.$emit('removeComponent', this.idx);
        }
    }
}
